/*==================== NAV ====================*/

.nav {
    max-width: 968px;
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .nav_logo, 
  .nav_toggle {
    color: var(--title-color);
    font-weight: var(--font-medium);
  }
  
  .nav_logo:hover {
    color: var(--first-color);
  }
  
  .nav_toggle {
    font-size: 1.1rem;
    cursor: pointer;
  }
  
  .nav_toggle:hover {
    color: var(--first-color);
  }
  
  @media screen and (max-width: 767px) {
    .nav_menu {
      position: fixed;
      bottom: -100%;
      left: 0;
      width: 100%;
      background-color: var(--body-color);
      padding: 2rem 1.5rem 4rem;
      box-shadow: 0 -1px 4px rgba(0, 0, 0, .15);
      border-radius: 1.5rem 1.5rem 0 0;
      transition: .3s;
    }
  }
  
  .nav_list {
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
  
  .nav_link {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--title-color);
    font-weight: var(--font-medium);
  }
  
  .nav_link:hover {
    color: var(--first-color)
  }
  
  .nav_icon {
    font-size: 1.2rem;
  }
  
  .nav_close {
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--first-color);
  }
  
  .nav_close:hover {
    color: var(--first-color-alt);
  }
  
/* show menu */
.show-menu {
  bottom: 0;
}

/* Active link */
.active-link a {
  color: var(--first-color);
}

/*==================== MEDIA QUERIES ====================*/
/* For small devices */
@media screen and (max-width: 350px) {
  .nav_menu {
    padding: 2rem .25rem 4rem
  }

  .nav_list {
    column-gap: 0 !important;
  }
}

/* For medium devices */

@media screen and (min-width: 768px) {
  .nav{
    height: calc(var(--header-height) + 1.5rem);
    column-gap: 1rem;
  }

  .nav_icon,
  .nav_close,
  .nav_toggle {
    display: none;
  }

  .nav_list {
    display: flex !important;
    column-gap: 2rem !important;
  }

  .nav_menu {
    margin-left: auto;
  }

  .change-theme {
    margin: 0;
  }
}


